/* From Uiverse.io by gharsh11032000 */ 
.card {
    top: 60px;
    position: relative;
    display: flex;
    /* align-items: center; */
    justify-content: center;
    width: 320px;
    border-radius: 24px;
    line-height: 1.6;
    transition: all 0.48s cubic-bezier(0.23, 1, 0.32, 1);
    flex-direction: column;
}

.content {

display: flex;
flex-direction: column;
align-items: flex-start;
/* gap: 24px; */
padding: 36px;
border-radius: 22px;
color: #333;
overflow: hidden;
background: radial-gradient( circle farthest-corner at 60% 90%,  rgba(255,94,247,1) 17.8%, rgba(2,245,255,1) 100.2% );
transition: all 0.48s cubic-bezier(0.23, 1, 0.32, 1);
}

.content::before {
position: absolute;
content: "";
top: -4%;
left: 50%;
width: 90%;
height: 90%;
transform: translate(-50%);
background: radial-gradient( circle farthest-corner at 40% 20%,  rgba(255,94,247,1) 17.8%, rgba(2,245,255,1) 100.2% );
z-index: -1;
transform-origin: bottom;
border-radius: inherit;
transition: all 0.48s cubic-bezier(0.23, 1, 0.32, 1);
}

.content::after {
position: absolute;
content: "";
top: -8%;
left: 50%;
width: 80%;
height: 80%;
transform: translate(-50%);
background: radial-gradient( circle farthest-corner at 60% 10%,  rgba(255,94,247,1) 17.8%, rgba(2,245,255,1) 100.2% );
z-index: -2;
transform-origin: bottom;
border-radius: inherit;
transition: all 0.48s cubic-bezier(0.23, 1, 0.32, 1);
}

.content svg {
width: 48px;
height: 48px;
}

.content .para {
z-index: 0;
opacity: 1;
font-size: 18px;
transition: all 0.48s cubic-bezier(0.23, 1, 0.32, 1);
}

.content .link {
z-index: 0;
color: #fea000;
text-decoration: none;
font-family: inherit;
font-size: 16px;
transition: all 0.48s cubic-bezier(0.23, 1, 0.32, 1);
}

.content .link:hover {

text-decoration: underline;
}

.card:hover {
cursor: pointer;
transform: translate(0px, -16px);
}

.card:hover .content::before {
rotate: -8deg;
top: 0;
width: 100%;
height: 100%;
}

.card:hover .content::after {
rotate: 8deg;
top: 0;
width: 100%;
height: 100%;
}