.TuduForm {
    /* border: 5px solid  #eaeaea0d; */
    border-radius: 10px;
    padding: 33px 40px;
    font-family: montserrat;
    font-size: 28px;
    /* background-color: transparent; */
    display: flex;
    width: clamp(250px, 50%, 500px);
    justify-content: center;
    align-items: center;
    flex-direction: column;
    z-index: 2;
    box-shadow: 0 0 5px #061de8a8, 0 0 10px #f4f4f4, 0 0 20px #d246f2,
    0 0 50px #e6fafd;
}
.TuduFormLabel {
    text-shadow: 0 0 5px #ffffff, 0 0 10px #ffffff, 0 0 20px #ffffff;
    text-align: center;
    /* font-weight: bold; */
    font-weight: 600;
    font-size: 22px;
    color: #fafafa;
}
.TuduFormTextarea {
    margin-top: 20px;
    color: #ffffff;
    background-color: transparent;
    border: 2PX solid #202329;
    border-radius: 3px;
    box-shadow: 10px 5px 50px rgba(23, 86, 211, 0.25);
    width: calc(100% - 25px); 
    font-size: 20px;
    height: 96px;
    padding: 12px;
}
.TuduFormTextarea::placeholder {
    color: #c0c0c0;
    font-family: montserrat;
    font-weight: 400;
    text-align: center;
    
}
.TuduFormTextarea:focus{
    outline-color: rgba(233, 234, 236, 0.135);
}
.ButtonContainer {
    margin-top: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 60px;
    /* flex-direction: row; */
}
.CancelButton, .SaveButton {
    margin: 10px;
    font-family: montserrat;
    border-radius: 10px;
    /* display: inline-block; */
    font-size: 20px;
    color: #000000;
    font-weight: 400;
    border: none;
    height: 48px;
    width: 130px;
    cursor: pointer;
}
.CancelButton {
    background-image: radial-gradient( circle farthest-corner at 10% 20%,  rgb(255, 94, 164) 17.8%, rgb(255, 2, 2) 100.2% );
    box-shadow: 0 0 5px #061de8, 0 0 10px #e20b0b, 0 0 20px #46e1f2,
    0 0 50px #e6fafd;

    /* margin-left: 20px; */
    /* margin-bottom: 50px; */
}
.SaveButton {
    /* outline: none; */
    background-image: radial-gradient( circle farthest-corner at 10% 20%,  rgb(226, 230, 223) 17.8%, rgb(6, 255, 2) 100.2% );
    box-shadow: 0 0 5px #061de8, 0 0 10px #0be20f, 0 0 20px #46e1f2,
    0 0 50px #e6fafd;
    /* margin-left: 30px; */

    /* margin-bottom: 50px; */
    
}
.CancelButton:hover {
    text-shadow: 0 0 5px #ffffff, 0 0 10px #ffffff, 0 0 20px #ffffff;
    box-shadow: 0 0px 5px #e80606, 0 10px 20px #e20b0b, 0 10px 50px #46e1f2,
    0 0 100px #e6fafd;
    animation: crecer 1s alternate infinite ease-in;
}

.SaveButton:hover {
    text-shadow: 0 0 5px #ffffff, 0 0 10px #ffffff, 0 0 20px #ffffff;
    box-shadow: 0 0px 5px #11e806, 0 10px 20px #1de20b, 0 10px 50px #46e1f2,
    0 0 100px #e6fafd;
    animation: crecer 1s alternate infinite ease-in;
}
@keyframes crecer {

    100% {transform: scale(1.2);}
}