.TuduCounter {
    font-family: montserrat;
    margin-top: 30px;
    box-sizing: border-box;
    height: 60px;
    /* cursor: none; */
    color: #1E1E1F;
    font-size: 24px;
    border-radius: 1rem;
    border: none;
    position: fixed;
    /* background: #e3deea; */
    transition: 0.1s;
    z-index: 2;
}

.TuduCounter::after {
content: '';
width: 100%;
height: 100%;
background-image: radial-gradient( circle farthest-corner at 60% 20%,  rgba(255,94,247,1) 17.8%, rgba(2,245,255,1) 100.2% );
filter: blur(15px);
z-index: -1;
position: absolute;
left: 0;
top: 0;
}

.TuduCounter:hover {
transform: scale(0.9) rotate(3deg);
background: radial-gradient( circle farthest-corner at 10% 20%,  rgba(255,94,247,1) 17.8%, rgba(2,245,255,1) 100.2% );
transition: 0.5s;
}
.TuduBackground{
    width: 100%;
    height: 100%;
    background-image: radial-gradient( circle farthest-corner at 10% 20%,  rgba(255,94,247,1) 17.8%, rgba(2,245,255,1) 100.2% );
    filter: blur(15px);
    z-index: -1;
    position: absolute;
    left: 0;
    top: 0;
}