#root {
  margin: 0 24px;
  position: relative;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  
}
* {
  box-sizing: border-box;
}

body {
  font-family: montserrat;
  --bg-color: url("../public/background-anime1.jpg");
  --text-color: #333;
  --faq-color: #1115;
  --dark-color: rgba(173, 205, 233, 0.55);

}
body, div, ul, li, a, h1, h2, h3, h4, p{
  margin: 0;
  padding: 0;
}
body {
  background-image: var(--bg-color);
  background-repeat: no-repeat, repeat;
  background-position:center;
  background-color: var(--dark-color);
  width: 100%;
  height: 100%;
  color: #1E1E1F;
  background-repeat: no-repeat;
  position: relative;
  width: 100%;
  height: 1490px;
  margin-left: auto;
  margin-right: auto;
}