.TuduSearchDiv {
    margin-top: 30px;
    width: 100%;
    display: flex;
    padding: 100px 20px 0 0;
    flex-direction: column;
    align-items: center;
}
.TuduSearchInput{
    
    width: 50%;
    padding: 15px ;
    border-radius: .5rem;
    font-size: 1.4rem;
    font-weight: 500;
    color: #1E1E1F;
    text-shadow: none;
    background: radial-gradient( circle farthest-corner at 10% 20%,  rgba(255,94,247,1) 17.8%, rgba(2,245,255,1) 100.2% );
    box-shadow: transparent;
    box-shadow: 0 0 5px #e806a8, 0 0 20px #db0be2, 0 0 50px #1b5ace,
    0 0 100px #e6fafd;
    border: none;
    transition: 0.2s ease;
    user-select: none;
    text-align: center;
    align-items: center;
    width: clamp(250px, 50%, 500px);

}

.TuduSearchInput:hover,.TuduSearchInput:focus{
    color: #ffffff;
    background: radial-gradient( circle farthest-corner at 10% 20%,  rgba(255,94,247,1) 17.8%, rgba(2,245,255,1) 100.2% );
    border: 3px solid #de2de8;
    text-shadow: 0 0 5px #ffffff, 0 0 10px #ffffff, 0 0 20px #ffffff;
    box-shadow: 0 0 5px #061de8, 0 0 20px #db0be2, 0 0 50px #46e1f2,
    0 0 100px #e6fafd;
}
.TuduSearchInput::placeholder {
    color: #333;
}